.error-template {
    text-align: center;
    top: 50%;
    left: 50%;
    position: absolute;
    display: block;
    transform: translate(-50%, -50%);
}

.error-actions {
    margin-top: 15px;
    margin-bottom: 15px;
    display: flex;
    justify-content: center;
}

.error-actions .btn {
    margin-right: 10px;
}

.error-template h1 {
    color: #022a54;
    font-weight: bold;
    font-size: 3rem;
}

@media screen and (max-width: 450px) {
    .error-template {
        text-align: center;
        top: 50%;
        left: 0;
        position: absolute;
        display: block;
        transform: translate(0%, -50%);
        width: 100%;
        padding: 0 20px;
    }
    .error-template h1 {
        font-size: 1.5rem;
    }
}